import { Exercicio } from './../../shared/exercicio.model';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServerServices } from '../../shared/serverServices.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-cadastrar-exercicio-personalizado',
  templateUrl: './cadastrar-exercicio-personalizado.component.html',
  styleUrls: ['./cadastrar-exercicio-personalizado.component.css'],
  providers: [ServerServices]
})
export class CadastrarExercicioPersonalizadoComponent implements OnInit {


  public exercicio: Exercicio;
  public form: FormGroup;

  public nome: string;
  public membro: string;
  public musculo: string;

  public musculosSuperiores: string[] = ["Peito", "Bíceps","Tríceps","Ombro","Costas","Antebraço", "Abdomen"]
  public musculosInferiores: string[] = ["Perna", "Gluteos","Panturrilha"]
  public musculos: string[]


  public selectMembro: any;

  constructor(private service: ServerServices, private router: Router, private formBuilder: FormBuilder) {

    this.form = formBuilder.group({
      nome: ['', [
        Validators.required,
        Validators.pattern(/[^0-9\.\,\"\?\!\;\:\#\$\%\&\(\)\*\+\-\/\<\>\=\@\[\]\\\^\_\{\}\|\~]+/)
      ]],
      membro: ['', [
        Validators.required,
      ]],
      musculo: ['', [
        Validators.required,
      ]]
    });
  }

  ngOnInit() {
  }

  enviar() {
    this.exercicio.nome = this.nome;
    this.exercicio.musculo = this.musculo;
    console.log(this.exercicio)

    this.service.servico('POST', 'exercicios', this.exercicio).subscribe(
      (res:any) => {
        console.log('RESPOSTA ', res)
      },
      (error: any) => {
        console.log('DEU RUIM ', error)
      }
    )
  }

  // Preenche dinamicamente a dropdown do musculo
  preencheMusculos($event){

    this.exercicio = new Exercicio()

    this.selectMembro = $event
    if(this.selectMembro == 0) {
      this.exercicio.membro = "Superior"
      this.musculos = this.musculosSuperiores
    } else {
      this.exercicio.membro = "Inferior"
      this.musculos = this.musculosInferiores
    }
    console.log(this.exercicio.membro)
  }

  pegaItem($event) {

    console.log(this.musculo)

  }

}



