import { SlideInOutAnimation } from './animation';
import { ServerServices } from './../../../../shared/serverServices.service';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-escolher-membro',
  templateUrl: './escolher-membro.component.html',
  styleUrls: ['./escolher-membro.component.css'],
  providers:[ServerServices],
  animations:[SlideInOutAnimation]
})
export class EscolherMembroComponent implements OnInit {

  lista_musculos = []
  grupo = true;
  mostraModal = false;
  grupoMuscular:any;
  selecionados = [];
  estadoAnimacao = 'out';
  contador = 0
  constructor( private service: ServerServices) {

   }

   MostrarModal(valor){
    this.mostraModal = valor
   }



   animar(e){
    if (e.estado === 'in'){
      this.selecionados[this.selecionados.indexOf(e)].estado = 'out'
      setTimeout(() => {
        this.selecionados[this.selecionados.indexOf(e)].buul = false
      },  1000);
    }else{
      this.selecionados[this.selecionados.indexOf(e)].buul = true

      setTimeout(()=>{
        this.selecionados[this.selecionados.indexOf(e)].estado = 'in'

      },  50);
    }
  }


   novosExercicios(e){
     e.forEach(exerc => {
       this.selecionados.push({"exercicios":exerc,"buul":false, "estado":'out', "id":this.contador })
       this.contador++
     });
   }

chamarExercicios(grupo){
  this.grupoMuscular = grupo
  this.MostrarModal(true)


}

   trocarGrupo(){

     this.grupo = !this.grupo
   }

  ngOnInit() {
  }

}



