import { ServerServices } from './../../../../shared/serverServices.service';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-escolher-exercicio',
  templateUrl: './escolher-exercicio.component.html',
  styleUrls: ['./escolher-exercicio.component.css'],
  providers:[ServerServices]
})
export class EscolherExercicioComponent implements OnInit {
@Input() grupoMuscular
@Input() mostraModal

@Output('toggleModal') toggleModal: EventEmitter<boolean> = new EventEmitter<boolean>();
@Output('checkeds') checkeds: EventEmitter<any> = new EventEmitter<any>()

selecionados = [];
exercicios = [];
  constructor(private service: ServerServices) { }


  ngOnInit() {
    this.service.servico("GET", "/exercicio/padroes", null).subscribe((data:any)=>{

      switch (this.grupoMuscular) {
        case 1:
          this.exercicios = data.grupoMuscular.superior.Peito

          break;
        case 2:
          this.exercicios = data.grupoMuscular.superior.Costas

          break;
        case 3 :
          this.exercicios = data.grupoMuscular.superior.Biceps

          break;
        case 4:
          this.exercicios = data.grupoMuscular.superior.Triceps

          break;
        case 5:
          this.exercicios = data.grupoMuscular.superior.Abdomen

          break;
        case 6:
          this.exercicios = data.grupoMuscular.superior.Antebraço

          break;
        case 7 :
          this.exercicios = data.grupoMuscular.superior.Ombro

          break;
        case 8:
          this.exercicios = data.grupoMuscular.inferior.Perna

          break;
        case 9:
          this.exercicios = data.grupoMuscular.inferior.Gluteos

          break;
        case 10:
          this.exercicios = data.grupoMuscular.inferior.Panturrilha
          break;
      }
    });



  }
  emitirLista(){
    this.checkeds.emit(this.selecionados)
    this.toggleModal.emit(false)
  }

  amostra(){
    this.toggleModal.emit(false)
  }



  selecionado(e, valor){
    console.log(e.target.checked)
    console.log(valor)
    if (e.target.checked){
      this.selecionados.push(valor)
    }else{
      this.selecionados.splice(this.selecionados.indexOf(valor), 1)
    }
    console.log(this.selecionados[0])

  }





}



