import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServerServices } from '../../shared/serverServices.service';

@Component({
  selector: 'app-meus-alunos',
  templateUrl: './meus-alunos.component.html',
  styleUrls: ['./meus-alunos.component.css'],
  providers: [ServerServices]
})
export class MeusAlunosComponent implements OnInit {


  public alunos: any[]
  temAlunos = true;
  constructor(private service: ServerServices, private router: Router) {

  }

  irParaCadastro(){
    this.router.navigate(['/addAluno'])
  }

  ngOnInit() {

    this.getAlunos()

  }

  getAlunos(){

    this.service.servico('GET', '/aluno', null).subscribe(
      (res: any) => {


        if (res[0] == null){
          this.temAlunos = false;
          console.log(this.alunos)

        }
        this.alunos = res
      }),
      (error: any) => {
        console.log('deu ruim')
      };

  }

}



